body {
  font-family: sans-serif;
  margin: 0;
  background-color: #7532a8;
  overflow-x: hidden;
}

p {
  margin-bottom: 0;
}

.row {
  margin: 0;
}

.centerOfWindow {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.redirect span {
  font-size: 36px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  text-align: center;
}

.redirect a {
  font-weight: bold;
  color: red;
}

.redirect a:hover {
  text-decoration: none;
}

.error {
  color: white;
  font-size: 24px;
  text-align: center;
  max-width: 500px;
  margin: 30px auto 0;
}
