.wrapper {
  position: absolute;
  top: 0;
  left: -200px;
}

.thread {
  background: grey;
  width: 4px;
  height: 100px;
  position: relative;
  animation: 2s swing ease-in-out infinite alternate forwards;
  transform-origin: 50% 0%;
}

.ball {
  position: absolute;
  top: 100%;
  left: 100%;
  transform: translateX(-50%);
  width: 150px;
  height: 150px;
}

@keyframes swing {
  from {
    transform: rotate(15deg);
  }
  to {
    transform: rotate(-15deg);
  }
}

@media screen and (max-width: 1199px) {
  .wrapper {
    display: none;
  }
}
