.authForm {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  width: 50%;
  max-width: 500px;
  min-width: 280px;
  font-size: 20px;
}

.authForm label:last-of-type {
  margin-top: 10px;
}

.authForm input {
  font-size: 20px;
  border: none;
  border-radius: 3px;
  display: block;
  padding: 5px;
  flex-grow: 1;
}

.authForm button {
  cursor: pointer;
  margin-left: 10px;
  display: block;
  font-weight: bold;
  color: white;
  background-color: #7532a8;
  border: 2px solid white;
  border-radius: 5px;
  padding: 5px 10px;
  transition: background-color 0.3s;
}

.authForm button:hover {
  background-color: #56257c;
}

@media screen and (max-width: 767px) {
  .authForm {
    flex-direction: column;
  }

  .authForm button {
    margin-left: 0;
    margin-top: 10px;
    align-self: center;
  }
}
