.wrapper {
  text-align: center;
}

.wrapper > div {
  position: relative;
  padding-top: 50px;
  margin: 0 auto 20px;

  width: 50%;
  max-width: 500px;
  min-width: 280px;
  font-size: 20px;
}

.wishForm {
  display: flex;
  flex-direction: column;
  border: 3px solid white;
  border-radius: 9px;
  padding: 10px;
}

.wishForm label {
  display: block;
  margin-bottom: 10px;
  color: white;
  text-align: left;
}

.wishForm label:last-of-type {
  margin-top: 10px;
}

.wishForm input,
.wishForm textarea {
  font-size: 20px;
  border: none;
  border-radius: 3px;
  display: block;
  flex-basis: 100%;
  padding: 5px;
}

.wishForm textarea {
  resize: none;
}

.wishForm button {
  cursor: pointer;
  margin-top: 10px;
  font-size: 20px;
  display: block;
  align-self: center;
  font-weight: bold;
  color: white;
  background-color: #7532a8;
  border: 2px solid white;
  border-radius: 5px;
  padding: 5px 10px;
  transition: background-color 0.3s;
}

.wishForm button:focus {
  outline: none;
}

.wishForm button:hover {
  background-color: #56257c;
}

.disabled {
  background-color: gray !important;
  cursor: not-allowed !important;
  transition: none !important;
}

@media screen and (max-width: 575px) {
  .wrapper > div {
    max-width: unset;
    width: 85%;
  }
}
