.item {
  width: 100%;
  height: 100%;
  font-family: "Balsamiq Sans", cursive;
}

.inner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 1s;
  transform-style: preserve-3d;
}

.item:hover .inner {
  transform: rotateY(180deg);
}

.front,
.back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
}

.back {
  color: white;
  transform: rotateY(180deg);
  border-radius: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.back p {
  padding: 10px;
  width: 100%;
  line-height: 1;
  font-weight: bold;
  font-size: 34px;
  text-align: center;
  position: relative;
}

.back span {
  font-family: sans-serif;
  position: absolute;
  display: block;
  font-weight: 400;
  font-size: 18px;
  left: 50%;
  top: 100%;
  width: 100%;
  transform: translateX(-50%);
}
